<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Quiz</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
        <ion-input :disabled="!editable" placeholder="Enter name" @ionChange="validate" v-model="name"></ion-input>
        <ion-input :disabled="!editable" placeholder="Enter description" @ionChange="validate" v-model="description"></ion-input>
        <ion-input :disabled="!editable" type="number" placeholder="Enter time amount" @ionChange="validate" v-model="timeAmount"></ion-input>
        <ion-input :disabled="!editable" placeholder="Enter password" @ionChange="validate" v-model="password"></ion-input>
        <div v-for="item in questions" v-bind:key="item">
            <ion-item>
                <ion-label>Resources </ion-label>
                <ion-select multiple="true" cancel-text="Cancel" ok-text="Select" v-model="item.resources" :disabled="!editable">
                    <ion-select-option v-for="resource in resources" v-bind:key="resource" :value="resource.id">{{resource.name}} ({{resource.description}})</ion-select-option>
                </ion-select>
            </ion-item>
            <ion-input :disabled="!editable" placeholder="Enter question" @ionChange="validate" v-model="item.question"></ion-input>
            <ion-input :disabled="!editable" placeholder="Enter answer/-s (separted by ';')" @ionChange="validate" v-model="item.answer"></ion-input>
            <div v-for="answer in item.studentAnswers" v-bind:key="answer">
                <p><b>{{answer.username}}</b> answered <b>{{answer.value}}</b> at {{answer.answeredAt}}</p>
                <br />
            </div>
        </div>
        <ion-button :disabled="!editable" expand="full" @click="addQuestion">Add question</ion-button>
        <ion-button :disabled="!editable" expand="full" @click="removeQuestion">Remove question</ion-button>
        <ion-button :disabled="!editable" expand="full" @click="submitQuiz">Submit</ion-button>
        <ion-button :disabled="!(id != 'new' && editable)" expand="full" @click="initiateQuiz">Initiate</ion-button>
    </ion-content>
  </ion-page>
</template>
<script>
import { IonContent, IonInput, IonButton, toastController, IonPage, IonToolbar, IonHeader, IonTitle, IonItem, IonLabel, IonSelect, IonSelectOption } from '@ionic/vue';
import { warning } from 'ionicons/icons';
import { defineComponent } from 'vue';
import { useRouter } from 'vue-router';
import axios from 'axios';

export default defineComponent({
  components: { IonInput, IonButton, IonContent, IonPage, IonToolbar, IonHeader, IonTitle, IonItem, IonLabel, IonSelect, IonSelectOption },
  setup() {
    const router = useRouter();
    return { warning, router };
  },
  created: function(){
    axios.get('api/resourceEntries').then((response) => {
      response.data.forEach(element => {
          this.resources.push({
              name: element.title,
              id: element.id,
              description: element.description
          })
      });
    });
    
     const parseJwt = (token) => {
            const base64Url = token.split('.')[1];
            const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
            const jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
                return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
            }).join(''));

            return JSON.parse(jsonPayload);
        };

    if (!parseInt(this.id)){
		this.id = "new";
        this.editable = true;
	}

    if (this.id != "new"){
        axios.get(process.env.VUE_APP_TEACHER_API_URL+'/quizmanagement/quiz/'+this.id).then((response) => {
            if (response.data.author.username == parseJwt(window.localStorage.getItem("teacherJwt")).Username){
                this.editable = true;
                response.data.questions.forEach(question => {
                    this.questions.push({
                        resources: question.resourceIds ? question.resourceIds.split(';').map( Number ) : [],
                        question: question.actualQuestion,
                        answer: question.actualAnswers,
                        studentAnswers: question.answers ?? [],
                    });
                });
                this.name = response.data.name;
                this.description = response.data.description;
                this.password = response.data.password;
            }

            this.timeAmount = response.data.timeAmount;
        }).catch((e)=>{
          this.failedSubmitToast();
        });
    }
    
  },
  methods: {
      initiateQuiz(){
          axios.post(process.env.VUE_APP_TEACHER_API_URL+'/quizmanagement/quiz/'+this.id+'/initiate').then(() => {
              this.successSubmitToast();
          }).catch(() => {
               this.failedSubmitToast();
          });
      },
      submitQuiz(){
          const payload = {
              name: this.name,
              description: this.description,
              timeAmount: this.timeAmount,
              password: this.password,
              questions: this.questions
          };

          if (this.id == 'new'){
              axios.post(process.env.VUE_APP_TEACHER_API_URL+'/quizmanagement/quiz', payload).then(() => {
                    this.successSubmitToast();
                    this.router.push("/tabs/quizzes");
                }).catch(() => {
                    this.failedSubmitToast();
                });
          }else{
              axios.put(process.env.VUE_APP_TEACHER_API_URL+'/quizmanagement/quiz/'+this.id, payload).then(() => {
                    this.successSubmitToast();
                }).catch(() => {
                    this.failedSubmitToast();
                });
          }
      },
      async failedSubmitToast() {
        const toast = await toastController
          .create({
            message: 'Operation failed. Perhaps you don\'t have access. Try again later.',
            duration: 2000
          })
        return toast.present();
      },
      async successSubmitToast() {
        const toast = await toastController
          .create({
            message: 'Operation succeeded.',
            duration: 2000
          })
        return toast.present();
      },
      addQuestion(){
          this.questions.push({
              resources: [],
              question: "",
              answer: ""
          })
      },
      removeQuestion(){
          this.questions.splice(-1,1);
      },
      validate(){
          return true;
      }
  },
  data: () => ({
    name: "",
    description: "",
    timeAmount: 0,
    password: "",
    questions: [],
    id: window.location.href.substring(window.location.href.lastIndexOf('/')+1,window.location.href.length),
    editable: false,
    resources: [],
  }),
});
</script>

